import React from 'react';
import {Avatar, Button} from "@mui/material";
import s from "./Users.module.scss";
import {follow, unfollow, UserType} from "../../redux/users-reducer";
import {NavLink} from 'react-router-dom';
import {useDispatch} from 'react-redux';
import axios from "axios";

type UsersType = {
    usersList: Array<UserType>
}

export const Users = React.memo(({usersList}: UsersType) => {
    const dispatch = useDispatch()
    return <div>
        <div className={s.users}>
            {usersList.map(u => {
                return (
                    <div key={u.id} className={s.user}>
                        <div>
                            <NavLink to={'/profile/' + u.id}>
                                <Avatar alt={u.name} src={u.photos?.small} sx={{width: 100, height: 100}}/>
                            </NavLink>
                        </div>
                        <div>
                            {u.followed
                                ? <Button onClick={() => {
                                    axios.delete(`https://social-network.samuraijs.com/api/1.0/follow/${u.id}`,  {
                                        withCredentials: true,
                                        headers: {
                                            'API-KEY': '1e90b645-3ab8-4f0b-b1bb-01b70c47396d'
                                        }
                                    })
                                        .then(response => {
                                            if (response.data.resultCode === 0) {
                                                dispatch(unfollow(u.id))
                                            }
                                        })
                                }} variant="contained"
                                          color="error"
                                          size='small'>Unfollow</Button>
                                : <Button onClick={() => {

                                    axios.post(`https://social-network.samuraijs.com/api/1.0/follow/${u.id}`, {}, {
                                        withCredentials: true,
                                        headers: {
                                            'API-KEY': '1e90b645-3ab8-4f0b-b1bb-01b70c47396d'
                                        }
                                    })
                                        .then(response => {
                                            if (response.data.resultCode === 0) {
                                                dispatch(follow(u.id))
                                            }
                                        })
                                }}
                                          variant="contained"
                                          color="success"
                                          size='small'>Follow</Button>}
                        </div>
                        <span>{u.name}</span>
                        <span>{u.status}</span>
                    </div>
                )
            })}
        </div>
    </div>
})

